.bttv-rs-anim-collapse {
  display: none;

  &.bttv-rs-anim-in {
    display: block;
  }

  tr&.bttv-rs-anim-in {
    display: table-row;
  }

  tbody&.bttv-rs-anim-in {
    display: table-row-group;
  }
}

.bttv-rs-anim-collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  transition: height var(--bttv-rs-anim-collapse-duration) ease,
    width var(--bttv-rs-anim-collapse-duration) ease, visibility var(--bttv-rs-anim-collapse-duration) ease;
}
